import {
  GridElement,
  FormElement,
  InputElement
} from '@/components/container/items'
import {
  grid,
  block,
  field,
  gridItem
} from './components/setup/index'
import gridConfig from './el-conf/grid-config'
import formConfig from './el-conf/form-config'
import inputConfig from './el-conf/input-config'

const config: any = {
  // 
  size: {},
  // 
  components: {
    grid: GridElement,
    form: FormElement,
    input: InputElement
  },
  //  T^T
  setupComponents: {
    grid,
    block,
    field,
    'grid-item': gridItem
  },
  // 
  props: {
    grid: gridConfig,
    form: formConfig,
    input: inputConfig
  },
  // 
  condition: {
    root: ["form"],
    form: ["input", "grid"]
  },
  // 
  element: [
    {
      el: "form",
      group: '',
      name: "",
      icon: "fa fa-th-list"
    },
    {
      el: "grid",
      group: '',
      name: "",
      icon: "fa fa-th-large"
    },
    {
      el: 'input',
      group: '',
      name: '',
      icon: 'fa fa-keyboard-o'
    }
  ]
}
export default config
